.login-page {
}



.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    height: 100vh;
    background-image: url("../assets/images/bg.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}

.login-form {
    width: 60%;
    padding: 3rem;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 5px 50px rgba(0,0,0,0.5);
}

.input-group {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

label {
    font-weight: bold;
    font-size: 1.5rem;
    display: block;
    margin-bottom: 1rem;
}

input {
    width: 100%;
    padding: 10px 0 10px 0;
    border: 1px solid #ccc;
    border-radius: 3px;
    font-size: 1.4rem;
    text-align: center;
}

.error {
    color: red;
    font-size: 1.3rem;
    margin-top: 5px;
}

.login-submit {
    min-width: 10rem;
    padding: 0.7rem 0 0.7rem 0;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1.5rem;
}

.login-submit:hover {
    background-color: #0056b3;
    scale: 1.05;
}

